.calc-input {
  height: 20px;
  border-radius: 5px;
  border-color: gray;
}

.calculator-container {
  display: flex;
  flex-direction: column;
}

@media (min-width: 576px) {
  .calculator-container {
    flex-direction: row;
  }
}

.calculator-input {
  margin-top: 5px;
  margin-right: 0px;
}

.calculator-cell {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 10px;
}

.calculator-cell label {
  display: inline-flex;
}

.calculator-cell .result {
  display: inline-flex;
  margin-right: 0px;
  justify-content: flex-start;
}

@media (min-width: 576px) {
  .calculator-cell {
    margin-bottom: 0px;
  }
  .calculator-input {
    margin-right: 20px;
  }
  .calculator-cell .result {
    justify-content: flex-end;
  }
}

.App {
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
  width: 80px;
}

.App-header {
  background-color: #222;
  color: white;
  height: 80px;
  padding: 0px;
}

.App-header-container {
  display: flex;
  flex-direction: row;
}

@media (min-width: 576px) {
  .App-header {
    height: 150px;
    padding: 20px;
  }
  .App-header-container {
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
}

.container {
  padding: 10px
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

